import React, { useState } from 'react';
import {
    Button,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    FormControl,
    FormLabel,
    Input,
    NumberInput,
    NumberInputField,
    FormHelperText,
    IconButton,
    Select,
    ButtonGroup,

} from '@chakra-ui/react';
import {
    ChevronDownIcon, DeleteIcon, CheckIcon, CloseIcon,
} from '@chakra-ui/icons';
function PropertiesContent() {
    const [selectedProperties, setSelectedProperties] = useState<string[]>([]);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [isEditing, setIsEditing] = useState(false);
    const [propertyValues, setPropertyValues] = useState<Record<string, string>>({});
    const [newMenuItem, setNewMenuItem] = useState('');
    const [isAddingCustomProperty, setIsAddingCustomProperty] = useState(false);
    const [customProperties, setCustomProperties] = useState<string[]>([]);

    const properties = ['Text', 'Number', 'Color', 'Visibility', 'Custom Property'];

    const handlePropertySelect = (property: string = 'Text') => {
        setSelectedProperties([...selectedProperties, property]);
        setIsMenuOpen(false);
        setIsEditing(false);
        setPropertyValues((prevValues) => ({ ...prevValues, [property]: '' }));

        if (property === 'Custom Property') {
            setSelectedProperties([...selectedProperties, property]);
            setIsMenuOpen(false);
            setIsEditing(false);
            setPropertyValues((prevValues) => ({ ...prevValues, [property]: '' }));

            setNewMenuItem('');
        }
    };

    const handleAddProperty = () => {
        setIsEditing(!isEditing);
        setIsAddingCustomProperty(false);
    };
    const handleInputChange = (property: string, value: string) => {
        setPropertyValues((prevValues) => ({ ...prevValues, [property]: value }));
    };

    const handleDeleteProperty = (property: string, customPropertyName?: string) => {
        const updatedProperties = selectedProperties.filter((p) => p !== property);
        setSelectedProperties(updatedProperties);
    
        const { [property]: deletedProperty, ...rest } = propertyValues;
        setPropertyValues(rest);
    
        if (property === 'Custom Property' && customPropertyName) {
            const updatedCustomProperties = customProperties.filter(
                (cp) => cp !== customPropertyName
            );
            setCustomProperties(updatedCustomProperties);
    
            const { [customPropertyName]: deletedCustomProperty, ...restCustom } = propertyValues;
            setPropertyValues(restCustom);
        }
    };
    
const handleAddMenuItem = () => {
        if (newMenuItem.trim() !== '') {
            setCustomProperties([...customProperties, newMenuItem]);
            handlePropertySelect("Custom Property");
            setPropertyValues((prevValues) => ({ ...prevValues, [newMenuItem]: '' }));
        }
        setIsAddingCustomProperty(false);
    };

    const handleCancelEdit = () => {
        setNewMenuItem('');
        setIsAddingCustomProperty(false);
    };

    const renderFormControl = () => {
        return selectedProperties.map((property, index) => {
            switch (property) {
                case 'Text':
                    return (
                        <div key={index} style={{
                            display: 'flex', alignItems: 'center', justifyContent: "center", flexDirection: "row"}}>
                            <FormControl
                                style={{
                                    marginRight: '10px'}}
                            >
                                <FormLabel>{property}</FormLabel>
                                <Input
                                    type='text'
                                    value={propertyValues[property]}
                                    onChange={(e) => handleInputChange(property, e.target.value)}
                                    style={{
                                        color: 'white',
                                        borderRadius: '5px',
                                        border: '1px solid grey',
                                        padding: '5px'}}
                                />
                            </FormControl>
                            <IconButton
                                aria-label='Delete'
                                icon={<DeleteIcon />}
                                onClick={() => handleDeleteProperty(property)}
                                style={{
                                    marginTop: '30px',
                                }}
/>
                        </div>
                    );
                case 'Number':
                    return (
                        <div key={index} style={{ display: 'flex', alignItems: 'center' }}>
                          <FormControl>
                            <FormLabel>{property}</FormLabel>
                            <NumberInput
                              value={propertyValues[property]}
                              onChange={(value) => handleInputChange(property, value)}
                            >
                              <NumberInputField />
                            </NumberInput>
                            
                          </FormControl>
                          <IconButton
                            aria-label='Delete'
                            icon={<DeleteIcon />}
                            onClick={() => handleDeleteProperty(property)}
                            style={{
                                marginTop: '30px',
                                marginLeft:"10px"
                            }}
                          />
                        </div>
                      );

                case 'Color':
                    return (
                        <div key={index} style={{
                            display: 'flex', alignItems: 'center', justifyContent: "center", flexDirection: "row"
                       }}>
                            <FormControl
                                style={{
                                    marginRight: '10px',
 }}
                            >
                                <FormLabel>{property}</FormLabel>
                                <Input
                                    type='color'
                                    value={propertyValues[property]}
                                    onChange={(e) => handleInputChange(property, e.target.value)}
                                    style={{
                                        color: 'white',
                                        borderRadius: '5px',
                                        border: '1px solid grey',
                                        padding: '5px',
                                    }}
                                />
                                
                            </FormControl>
                            <IconButton
                                aria-label='Delete'
                                icon={<DeleteIcon />}
                                onClick={() => handleDeleteProperty(property)}
                                style={{
                                    marginTop: '30px',
                                }}
                            />
                        </div>
                    );
                case 'Visibility':
                    return (
                        <div key={index} style={{
                            display: 'flex', alignItems: 'center', justifyContent: "center", flexDirection: "row"
                      }}>
                            <FormControl
                                style={{
                                    marginRight: '10px',  }}
                            >
                                <FormLabel>{property}</FormLabel>
                                <Select
                                    value={propertyValues[property]}
                                    onChange={(e) => handleInputChange(property, e.target.value)}
                                    style={{
                                        color: 'white',
                                        borderRadius: '5px',
                                        border: '1px solid grey',
                                        padding: '5px',
                                    }}
                                >
                                    <option
                                        style={{
                                            color: 'black',

                                        }}
                                        value='true'>true</option>
                                    <option value='false'
                                        style={{
                                            color: 'black',

                                        }}
                                    >false</option>
                                </Select>
                                
                            </FormControl>
                            <IconButton
                                aria-label='Delete'
                                icon={<DeleteIcon />}
                                onClick={() => handleDeleteProperty(property)}
                                style={{
                                    marginTop: '30px',
                                }}
                            />
                        </div>
                    );
                case 'Custom Property':
                    const customPropertyName = customProperties[index]; 
                    return (
                        <div key={index} style={{ display: 'flex', alignItems: 'center' ,justifyContent :"center",flexDirection:"row"
                 }}>
                      <FormControl
                       style={{
                        marginRight: '10px',
                     
                      }}
                      >
                                <FormLabel>{customPropertyName}</FormLabel>
                                <Input
                                    type='text'
                                    value={propertyValues[customPropertyName]}
                                    onChange={(e) => handleInputChange(customPropertyName, e.target.value)}
                                    style={{
                                        color: 'white',
                                
                                        borderRadius: '5px',
                                        border: '1px solid grey',
                                        padding: '5px',
                                     
                                      }}
                                />
                            </FormControl>
                            <IconButton
                                aria-label='Delete'
                                icon={<DeleteIcon />}
                                onClick={() => handleDeleteProperty("Custom Property", customPropertyName)}
                                style={{
                                    marginTop: '30px',
                                }}
                            />
                        </div>
                    );


                default:
                    return null;
            }
        });
    };




    return (
        <div
            style={{
                display: 'flex',
                flexDirection: 'column',
                justifyContent: 'center',
                alignItems: 'center',
                marginTop: '10px',

            }}
        >
            {selectedProperties.length > 0 && (
                <div>
                    {renderFormControl()}
                </div>
            )}
            <Button
                style={{
                    marginTop: '10px',
                    border: '1px solid grey',
                    color: 'blue',
                    background: 'white',
                    borderRadius: '5px',             }}
                onClick={handleAddProperty}>Add Property</Button>
            <div style={{
                display: isEditing ? 'flex' : 'none', flexDirection: "row", width: "65%", justifyContent: "space-between", alignItems: "center"
            }}>
                <Menu

                    isOpen={isMenuOpen}
                    onOpen={() => setIsMenuOpen(true)}
                    onClose={() => setIsMenuOpen(false)}
                >
                    <MenuButton as={Button} rightIcon={<ChevronDownIcon />}
                        style={{
                            marginTop: '10px',
                            border: '1px solid grey',
                            background: 'white',
                            borderRadius: '5px',
                            width: '200px',
                     }}
                    >
                        Type
                    </MenuButton>
                    <MenuList>
                        {properties.map((property, index) => (
                            <MenuItem
                                key={index}
                                onClick={() => {
                                    if (property === 'Custom Property') {
                                        setIsAddingCustomProperty(true);
                                        setIsEditing(false);
                                    } else {
                                        handlePropertySelect(property);
                                    }
                                }}
                                style={{
                                    color: 'black',
                                }}
                            >
                                {property}
                            </MenuItem>
                        ))}
                    </MenuList>
                    <ButtonGroup
                        style={{
                            marginTop: '10px',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            marginLeft: '10px',  }}

                    >
                        <IconButton
                            icon={<CheckIcon />}
                            aria-label='Submit'
                            type='submit'
                            onClick={handleAddProperty}

                        />
                        <IconButton
                            icon={<CloseIcon />}
                            aria-label='Cancel'
                            onClick={handleAddProperty}
                        />
                    </ButtonGroup>
                </Menu>

            </div>

            {isAddingCustomProperty && (
                <div style={{  display: 'flex', alignItems: "inherit"
                , justifyContent: "center", flexDirection: "row",marginTop:"10px"
                 }}>
                    <FormControl>
                        <FormLabel>Custom Property Name</FormLabel>
                        <Input
                            type='text'
                            value={newMenuItem}
                            onChange={(e) => setNewMenuItem(e.target.value)}
                            style={{
                                color: 'white',
                                borderRadius: '5px',
                                border: '1px solid grey',
                                padding: '5px',
                            }}
                        />
                    </FormControl>
                    <ButtonGroup justifyContent='center' marginTop='30px' marginLeft="30px"
                    >
                        <IconButton
                            icon={<CheckIcon />}
                            aria-label='Submit'
                            type='submit'
                            onClick={handleAddMenuItem}
                        />
                        <IconButton
                            icon={<CloseIcon />}
                            aria-label='Cancel'
                            onClick={handleCancelEdit}
                        />
                    </ButtonGroup>
                </div>
            )}


        </div>
    );
}

export default PropertiesContent;




